import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {OraModal} from "eshop/components/OraCommonComponents";
import {ResponsiveVisibility} from "eshop/modules/core/components/ui/RWDUtils";
import {DetailsRaw} from "eshop/modules/core/components/ui/OfferDetails";

class CartDetailsModal extends Component  {
    constructor (props) {
        super(props);
    }

    renderTables(){
        console.log("details for:");
        console.log(this.props);
        return (
            <DetailsRaw data={this.props.details} showNet={this.props.showNet} proposition={this.props.proposition} style={styles["large"]}/>
        )
    }

    renderSimple(){
        var detailsHTML = {__html: this.props.details};
        return (this.props.details &&
            <div dangerouslySetInnerHTML={detailsHTML}></div>
        );
    }

    render(){
        var details = (this.props.details && typeof this.props.details === 'object') ? this.renderTables() : this.renderSimple();
        return(
        <OraModal id={this.props.id} showClose={true}  medium={true}>
            {details}
        </OraModal>)
    }
}

CartDetailsModal.defaultProps = {
    id: "",
    details: [],
    header : "default: Szczegóły oferty"
}

CartDetailsModal.propTypes = {
     id: PropTypes.string,
     details: PropTypes.oneOfType([
        PropTypes.arrayOf(PropTypes.object),
        PropTypes.string
     ]),
     header: PropTypes.string
 }

const styles = {
    "small": {
        "table": "u-block",
        "tbody": "u-block",
        "tr": "u-block u-border u-no-border-t u-no-border-l u-no-border-r",
        "th": {
            "first": "u-block u-text-left",
            "rest": "u-padding-top u-block u-text-left"
        },
        "td": {
            "first": "u-padding u-block u-font-bold",
            "rest": "u-padding u-block u-font-bold"
        }
    },
    "medium": {
        "table": "u-table-fixed u-w-100",
        "tbody": "",
        "tr": "u-border u-no-border-t u-no-border-l u-no-border-r",
        "th": {
            "first": "u-padding u-padding-right u-text-left",
            "rest": "u-padding u-padding-top u-padding-right u-text-left"
        },
        "td": {
            "first": "u-padding u-padding-left u-font-bold",
            "rest": "u-padding u-padding-top u-padding-left u-font-bold"
        }
    },
    "large": {
        "table": "u-table-fixed u-w-100",
        "tbody": "",
        "tr": "u-border u-no-border-t u-no-border-l u-no-border-r",
        "th": {
            "first": "u-padding u-padding-right u-text-left",
            "rest": "u-padding u-padding-top u-padding-right u-text-left"
        },
        "td": {
            "first": "u-padding u-padding-left u-font-bold",
            "rest": "u-padding u-padding-top u-padding-left u-font-bold"
        }
    }
}

export default CartDetailsModal;